export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {}
		};
	},
	methods: {
		goTo () {
			uni.navigateTo({
				url: '../registerLogin/registerLogin'
			})
		}
	},
	onShow() {
		let user = uni.getStorageSync('user')
		if (user) {
			this.userIsLogin = true
			this.userInfo = user
		} else {
			this.userIsLogin = false
		}
	}
}
